import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  [theme.breakpoints.up("xs")]: {
    "mobile-integrations": {
      overflow: "hidden",
      width: "100%",

      "& > .header": {
        alignItems: "center",
        borderBottom: "1px solid #24272c1a",
        display: "flex",
        gap: "20px",
        height: "70px",
        padding: "20px",
        width: "100%",

        "& > a": {
          alignItems: "center",
          display: "flex",
          height: "100%",
        },

        "& .title": {
          color: "#24272c",
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
        },
      },

      "& .content": {
        display: "flex",
        flexDirection: "column",
        rowGap: "36px",
        width: "100%",

        "& .card-group": {
          padding: "30px 30px 40px",

          "& .card-title": {
            color: "#24272C",
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "21px",
            marginBottom: "30px",
          },

          "& .integration-block": {
            display: "flex",
            flexDirection: "row",
            gap: "15px 10px",
            flexWrap: "wrap",
            backgroundColor: "#ffffff",
            marginBlock: "30px",

            "& .card-item": {
              borderRadius: "8px",
              padding: "11px 10px",
              width: "calc(50% - 10px)",
              height: "100%",
              marginBottom: "10px",
              background: "#FFFFFF",
              border: "1px solid rgba(0, 0, 0, 0.05)",

              "&:hover": {
                boxShadow: "2px 2px 20px rgba(0, 0, 0, 0.15)",
              },

              "& .card-icon": {
                height: "36px",
                width: "36px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                marginBottom: "8px",

                "& img": {
                  height: "100%",
                  width: "100%",
                },
              },

              "& .card-title": {
                color: "#24272C",
                fontWeight: "700",
                fontSize: "14px",
                lineHeight: "16px",
                marginBottom: "2px",
              },

              "& .card-subtitle": {
                color: "#24272C",
                fontSize: "16px",
                fontWeight: 400,
                lineHeight: "16px",
              },

              "& .card-description": {
                color: "#8B8E92",
                fontSize: "8px",
                fontWeight: 700,
                lineHeight: "10px",
              },

              "& .card-status": {
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                marginTop: "10px",

                "& .card-link": {
                  color: "#ADBBC5",
                  fontSize: "8px",
                  fontWeight: 700,
                  lineHeight: "9px",
                  textDecoration: "none",
                  textAlign: "right",
                  display: "flex",
                  alignItems: "center",
                  gap: "2px",
                },

                "& .card-price": {
                  color: "#4AB54F",
                  fontSize: "8px",
                  fontWeight: 700,
                  lineHeight: "9px",
                },
              },
            },
          },

          "& .field": {
            marginBottom: "30px",

            "& .MuiFormControl-root": {
              width: "100%",
            },
          },
        },

        "& .chatbot-card": {
          alignItems: "center",
          backgroundColor: "#12B0EE",
          borderRadius: "10px",
          boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
          display: "flex",
          flexDirection: "column",
          justifyContent: "end",
          margin: "0 30px 40px",
          padding: "35px 18px 16px",
          rowGap: "10px",

          "& .bot-image": {
            display: "flex",
            flexShrink: 1,
            width: "194px",
            maxWidth: "100%",
            height: "auto",
          },

          "& .bot-info": {
            display: "flex",
            flexDirection: "column",
            flexGrow: 1,

            "& .quote": {
              color: "#ffffff",
              fontSize: "14px",
              fontWeight: 400,
              lineHeight: "18px",

              "& .strong": {
                fontWeight: 700,
              },
            },

            "& .bot-actions": {
              alignItems: "center",
              display: "flex",
              justifyContent: "flex-end",

              "& .MuiButton-root": {
                backgroundColor: "#FFFFFF",
                boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
                borderRadius: "8px",
                color: "#12B0EE",
                columnGap: "8px",
                display: "inline-flex",
                height: "48px",
                marginBottom: "9px",
                marginTop: "18px",
              },
            },
          },
        },

        "& .action": {
          display: "flex",
          justifyContent: "end",

          "& .MuiButton-root": {
            "& .MuiButton-label": {
              "& .text-limiter": {
                overflow: "hidden",
                textOverflow: "ellipsis",
                whiteSpace: "nowrap",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("sm")]: {
    "form-stacked-cards": {
      display: "flex",
      flexDirection: "column",
      paddingBottom: "50px",
      paddingTop: "50px",
      maxWidth: "650px",
      rowGap: "36px",
      width: "100%",

      "& .card-group": {
        backgroundColor: "#ffffff",
        borderRadius: "10px",
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
        padding: "30px 36px 40px",

        "&.inactive": {
          alignItems: "center",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          paddingTop: 0,
          rowGap: "42px",
          textAlign: "center",

          "& .info": {
            display: "flex",
            flexDirection: "column",
            rowGap: "18px",
            maxWidth: "400px",

            "& .desc": {
              color: "#24272CB3",
              fontSize: "15px",
              fontWeight: 400,
              lineHeight: "18px",

              "& .MuiLink-root": {
                color: "#12B0EE",
              },
            },
          },
        },

        "& .card-title": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "21px",
          marginBottom: "30px",
        },

        "& .flex-row": {
          display: "flex",
          flexDirection: "column-reverse!important",
          gap: "30px 35px",

          "&.min-gap": {
            flexDirection: "column!important",
            columnGap: "18px",
            rowGap: "0px",
          },

          "&.check-to-fill": {
            alignItems: "center",
            backgroundColor: "#E9F1F2",
            height: "60px",
            justifyContent: "center",
            margin: "0 -36px 30px",
            padding: "9px 0",

            "& .centered-colum": {
              padding: "0 36px",

              "& .MuiFormGroup-root": {
                "& .MuiFormControlLabel-root": {
                  marginRight: 0,

                  "& .MuiCheckbox-root": {
                    color: "#24272C80",

                    "&.Mui-checked": {
                      color: "#12B0EE",
                    },
                  },

                  "& .MuiFormControlLabel-label": {
                    color: "#24272C",
                    fontSize: "16px",
                    fontWeight: 400,
                    lineHeight: "19px",
                  },
                },
              },
            },
          },
        },

        "& .field": {
          marginBottom: "30px",

          "& .MuiFormControl-root": {
            width: "100%",
          },

          "& .MuiInputBase-root": {
            "&::before, &::after": {
              display: "none",
            },
          },
        },
      },

      "& .integration-block": {
        display: "flex",
        flexDirection: "row",
        gap: "25px 19px",
        flexWrap: "wrap",
        backgroundColor: "#ffffff",
        marginBlock: "30px",

        "& .card-item": {
          borderRadius: "8px",
          padding: "11px 10px",
          width: "130px",
          height: "100%",
          flex: "0 0 130px",
          marginBottom: "30px",
          background: "#FFFFFF",
          border: "1px solid rgba(0, 0, 0, 0.05)",

          "&:hover": {
            boxShadow: "2px 2px 20px rgba(0, 0, 0, 0.15)",
          },

          "& .card-icon": {
            height: "36px",
            width: "36px",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            marginBottom: "8px",

            "& img": {
              height: "100%",
              width: "100%",
            },
          },

          "& .card-title": {
            color: "#24272C",
            fontWeight: "700",
            fontSize: "14px",
            lineHeight: "16px",
            marginBottom: "2px",
          },

          "& .card-subtitle": {
            color: "#24272C",
            fontSize: "16px",
            fontWeight: 400,
            lineHeight: "16px",
          },

          "& .card-description": {
            color: "#8B8E92",
            fontSize: "8px",
            fontWeight: 700,
            lineHeight: "10px",
          },

          "& .card-status": {
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            marginTop: "10px",

            "& .card-link": {
              color: "#ADBBC5",
              fontSize: "8px",
              fontWeight: 700,
              lineHeight: "9px",
              textDecoration: "none",
              textAlign: "right",
              display: "flex",
              alignItems: "center",
              gap: "2px",
            },

            "& .card-price": {
              color: "#4AB54F",
              fontSize: "8px",
              fontWeight: 700,
              lineHeight: "9px",
            },
          },
        },
      },

      "& .chatbot-card": {
        alignItems: "flex-end",
        backgroundColor: "#12B0EE",
        borderRadius: "10px",
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
        columnGap: "10px",
        display: "flex",
        height: "190px",
        marginTop: "4px",
        padding: "35px 18px 16px",

        "& .bot-image": {
          display: "flex",
          flexShrink: 1,
          width: "calc(100% - 200px)",
          maxWidth: "100%",
          height: "auto",
        },

        "& .bot-info": {
          display: "flex",
          flexDirection: "column",
          flexGrow: 1,

          "& .quote": {
            color: "#ffffff",
            fontSize: "14px",
            fontWeight: 400,
            lineHeight: "18px",

            "& .strong": {
              fontWeight: 700,
            },
          },

          "& .bot-actions": {
            alignItems: "flex-end",
            display: "flex",
            justifyContent: "space-between",

            "& .MuiButton-root": {
              backgroundColor: "#FFFFFF",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              borderRadius: "8px",
              color: "#12B0EE",
              columnGap: "8px",
              display: "inline-flex",
              height: "48px",
              marginBottom: "9px",
              marginTop: "18px",
            },
          },
        },
      },

      "& .action": {
        display: "flex",
        justifyContent: "end",

        "& .MuiButton-root": {
          "& .MuiButton-label": {
            "& .text-limiter": {
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "form-stacked-cards": {
      "& .card-group": {
        "& .flex-row": {
          flexDirection: "row!important",

          "&.min-gap": {
            flexDirection: "row!important",
          },

          "& .half": {
            flex: "0 0 auto",
            width: "calc((100% - 18px)/2)",
          },

          "& .grow-colum": {
            flexGrow: 1,
          },

          "& .shrink-colum": {
            flexShrink: 1,
          },
        },

        "& .field": {
          "&.space-top": {
            marginTop: "30px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
