/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment } from "react";
import { Hidden } from "@mui/material";

// import MobileSettings from "Container/BusinessSettings/Whatsapp/settings/Mobile";
import DesktopIntegrations from "Container/BusinessSettings/Whatsapp/integrations/Desktop";
import MobileIntegrations from "Container/BusinessSettings/Whatsapp/integrations/Mobile";

import Layout from "../Layout";

const WABusinessProfileSettings = (props) => {
  return (
    <Fragment>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopIntegrations />
        </Layout>
      </Hidden>
      <Hidden smUp implementation="js">
        <MobileIntegrations />
      </Hidden>
    </Fragment>
  );
};

export default WABusinessProfileSettings;
